<template>
  <section class="prescription__wrap box__shadow__01 vertical__scroll">
    <div class="prescription__top">
      <h3 class="f__bold">처방전</h3>
      <ul>
        <li>
          <button class="custom__btn" @click="prescriptionTemSave">
            <p>템플릿 저장</p>
          </button>
        </li>
      </ul>
    </div>
    <div class="prescription__bottom">
      <div
        class="week__list__item"
        v-for="(week, weekIdx) in prescriptionListByWeek"
        :data-index="weekIdx"
        :key="weekIdx"
      >
        <div class="title__wrap">
          <h4 :data-week="weekIdx">{{ weekIdx + 1 }}주차</h4>
          <button
            class="custom__btn delete small"
            @click="cleanWeekData(weekIdx)"
          >
            <span>
              <img src="@/assets/images/icon/ic-remove.svg" alt="삭제 아이콘" />
            </span>
            <p>비우기</p>
          </button>
        </div>
        <ul>
          <li
            class="box__shadow__03"
            v-for="(date, dateIdx) in week.treatmentList"
            :data-index="dateIdx"
            :key="dateIdx"
            @dragover="dragOver($event)"
            @drop="drop($event, 'add')"
            @dblclick="treatmentPreview(date.id, date.dayNum, weekIdx + 1)"
          >
            <p class="day__num" v-if="date.id === ''">
              {{ dateIdx + 1 }}
            </p>
            <dl
              v-if="typeof date.id !== ''"
              draggable="true"
              @dragend="removeDragEnd($event)"
              @dragover="removeDragOver"
              @dragleave="removeDragStart($event)"
            >
              <dt>{{ date.categoryName }}</dt>
              <dd>{{ date.name }}</dd>
            </dl>
          </li>
        </ul>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  props: {
    dragRemedyItem: [Object, String],
  },
  watch: {
    dragRemedyItem() {
      this.dargItemAddFlag = true;
    },
  },
  data() {
    return {
      prescriptionListByWeek: [],
      dargItemAddFlag: false,
    };
  },
  methods: {
    drop(evt) {
      if (this.dargItemAddFlag) {
        evt.preventDefault();
        if (evt.srcElement.tagName === "P") {
          const weekIndex = Number(
            evt.srcElement.parentNode.parentNode.parentNode.childNodes[0]
              .childNodes[0].dataset.week
          );
          const dateIndex = Number(evt.srcElement.parentNode.dataset.index);
          const treatmentObj =
            this.prescriptionListByWeek[weekIndex].treatmentList[dateIndex];
          treatmentObj.id = this.dragRemedyItem.id;
          treatmentObj.categoryName = this.dragRemedyItem.categoryName;
          treatmentObj.name = this.dragRemedyItem.name;
          treatmentObj.dayNum = dateIndex;
        }
        this.dargItemAddFlag = false;
        this.treatmentPreviewFlag = false;
      }
    },
    removeDragOver() {
      this.dargItemRemoveFlag = true;
    },
    removeDragStart() {
      this.dargItemRemoveFlag = false;
    },
    removeDragEnd(evt) {
      if (!this.dargItemRemoveFlag) {
        const weekIndex = Number(
          evt.srcElement.parentNode.parentNode.parentNode.childNodes[0]
            .childNodes[0].dataset.week
        );
        const dateIndex = Number(evt.srcElement.parentNode.dataset.index);
        this.prescriptionListByWeek[weekIndex].treatmentList.splice(
          dateIndex,
          1,
          { id: "" }
        );
      }
      this.treatmentPreviewFlag = false;
    },
    dragOver(evt) {
      evt.preventDefault();
    },
    cleanWeekData(param) {
      this.prescriptionListByWeek[param].treatmentList.map((obj, idx) => {
        this.prescriptionListByWeek[param].treatmentList.splice(idx, 1, {
          dayNum: 1,
          id: "",
          preLearningWeekNum: null,
          preLearningDayNum: null,
          categoryName: "",
          name: "",
        });
      });
      this.treatmentPreviewFlag = false;
    },
    prescriptionTemSave() {
      this.$emit("templateSavePopOpen", this.prescriptionListByWeek.slice());
    },
  },
  mounted() {
    const params = this.$route.params;
    const prescriptionListByWeek = params.prescriptionListByWeek;
    this.prescriptionListByWeek = this.$store.state.prescription(0);
    if (prescriptionListByWeek !== undefined) {
      for (let i = 0; i < prescriptionListByWeek.length; i++) {
        let weekNum =
          prescriptionListByWeek.length === 1
            ? 0
            : prescriptionListByWeek[i].weekNum - 1;
        for (
          let j = 0;
          j < prescriptionListByWeek[i].treatmentList.length;
          j++
        ) {
          if (prescriptionListByWeek[i].treatmentList[j].id !== null) {
            this.prescriptionListByWeek[weekNum].treatmentList.map(
              (obj, idx) => {
                if (
                  obj.dayNum ===
                  prescriptionListByWeek[weekNum].treatmentList[j].dayNum
                ) {
                  Object.assign(
                    this.prescriptionListByWeek[weekNum].treatmentList[idx],
                    prescriptionListByWeek[weekNum].treatmentList[j]
                  );
                }
              }
            );
          }
        }
      }
    }
  },
};
</script>
